import React from "react";
import { useState } from "react";

export default function useChatMessages(initValue) {
    const [ messages, setMessages ] = useState(initValue);

    const prependMsgs = (preMsgs) => {
        // setMessages(messages.concat(preMsgs))
        setMessages(preMsgs.concat(messages));
    }
    const appendMsgs = (appMsgs) => {
        setMessages(messages.concat(appMsgs));
    }

    return {
        messages: messages,
        prependMsgs: prependMsgs,
        appendMsgs: appendMsgs,
      }
}