import react from "react";
import { Bubble, Video, SystemMessage, Text } from "@chatui/core";
import { Image } from 'antd';
import parser from 'xml2js';

export default function renderMessageContent(msg)
{
	switch (msg.type) {
		case '1': //ÊñáÊú¨
			return <Bubble content={msg.content.content} id={msg._id}/>;
			// return (<Bubble id={msg._id}>
			// 	<div style={{wordWrap: 'break-word', width: '100%', overflowX: 'hidden'}}>
			// 		<text>{msg.content.content}</text>
			// 	</div>
			// </Bubble>);
		case '3': //ÂõæÁâá
			return (
				<Bubble type="image" id={msg._id}>
					<Image src={msg.content.mediaSourcePath || msg.content.mediaBCKPath || msg.content.mediaPath} 
						alt="ÂõæÁâá" 
					/>
				</Bubble>
			);
		case '34': //ËØ≠Èü≥
			return (<Bubble id={msg._id}>
				<audio controls src={msg.content.mediaPath} />
			</Bubble>);
		case "43": // ËßÜÈ¢ë
			return (<Bubble type="video" id={msg._id}>
				<Video src={ msg.content.mediaPath } />
				</Bubble>);
		case "47": // Â§ßË°®ÊÉÖ
			return (<Bubble type="image" id={msg._id}>
				<img src={msg.content.emojiInfo.cdnUrl} alt="Ë°®ÊÉÖÊó†Ê≥ïÂä†ËΩΩ" />
				</Bubble>);
		case "48":
			return <Bubble content={"[‰ΩçÁΩÆÊ∂àÊÅØüòÇ]"} id={msg._id}/>;
		case "49":
			// Âç°Áâá‰ø°ÊÅØ
			return <Bubble content={"[Âç°Áâá‰ø°ÊÅØ]"} id={msg._id}/>;
		case "10000":
			// Á≥ªÁªüÊ∂àÊÅØ
			return <div style={{ margin: "0 auto" }} id={msg._id} ><SystemMessage content={msg.content.content} /></div>;
		case "268445456":
			// Êí§ÂõûÊ∂àÊÅØÈÄöÁü•
			return <div style={{ margin: "0 auto" }} id={msg._id}><SystemMessage content={msg.content.content} /></div>;
		case "436207665":
			// ÂæÆ‰ø°Á∫¢ÂåÖ
			return "text";
		case "419430449":
			// ÂæÆ‰ø°ËΩ¨Ë¥¶
			return "text";
		case "1090519089":
			// Êñá‰ª∂
			return "";
		case "318767153":
			// ÂÖ¨‰ºóÂè∑Êé®ÈÄÅ
			return "text";
		case "822083633": //Ê∂àÊÅØÂºïÁî®
			// console.log("parseString ++", msg.content.msgId);
			let finalmsg = undefined;
			parser.parseString(msg.content.content, (error, result) => {
				if (!error) {
					// console.log(result);
					let refermsg = result.msg.appmsg[0].refermsg[0].displayname[0] + ": ";
					if (result.msg.appmsg[0].refermsg[0].type[0] === '1') { //ÂºïÁî®ÊñáÂ≠ó
						refermsg += result.msg.appmsg[0].refermsg[0].content[0];
					} else if (result.msg.appmsg[0].refermsg[0].type[0] === '43') { //ÂºïÁî®ËßÜÈ¢ë
						refermsg += '[ËßÜÈ¢ë]';
					} else if (result.msg.appmsg[0].refermsg[0].type[0] === '3') { //ÂºïÁî®ÂõæÁâá
						refermsg += '[ÂõæÁâá]';
					} else if (result.msg.appmsg[0].refermsg[0].type[0] === '34') { //ÂºïÁî®ËØ≠Èü≥
						refermsg += '[ËØ≠Èü≥]';
					} else if (result.msg.appmsg[0].refermsg[0].type[0] === '49') { //ÂºïÁî®‰∫ÜÂºïÁî®
						let reReferMsg = undefined;
						parser.parseString(result.msg.appmsg[0].refermsg[0].content[0], (error, result) => {
							if (!error) {
								reReferMsg = result.msg.appmsg[0].title? result.msg.appmsg[0].title[0]: "Êú™Áü•ÂºïÁî®ÁöÑÂºïÁî®";
							} else {
								reReferMsg = "ÂºïÁî®ÁöÑÂºïÁî®xmlËß£ÊûêÂ§±Ë¥•";
							}
						});
						refermsg = '[ÂºïÁî®]\n' + refermsg;
						refermsg += reReferMsg;
					} else {
						refermsg += "ÈúÄË¶ÅÂ§ÑÁêÜÁöÑÂºïÁî®Á±ªÂûã" + result.msg.appmsg[0].refermsg[0].type[0];
					}
					let appmsg = result.msg.appmsg[0].title[0];
					finalmsg = (<Bubble id={msg._id}>
						<Text breakWord>{refermsg}</Text>
						<Text>----</Text>
						<Text>{appmsg}</Text>
					</Bubble>);
				} else {
					console.error('XML parsing error: ', error);
					finalmsg = (<Bubble type='text' content='XML parsing error: {error}'/>);
				}
			});
							
			// console.log("parseString --");
			return finalmsg;
		case "922746929": //Êãç‰∏ÄÊãç
		default:
			console.log(msg);
			return <Bubble content={`Êú™Áü•Á±ªÂûã: ${msg.type}`} id={msg._id}/>
	}
}