import React from 'react';
import { useState, useEffect } from 'react';
import { Input } from '@chatui/core';
import axios from 'axios';
import { Divider } from 'antd';
import { nanoid } from 'nanoid';

import './userlist-theme.css'
import UserItem from './UserItem';


export default function(props) {
  const [ filterText, setFilterText ] = useState('');
  const [ users, setUsers ] = useState([]);
  const [ isLoading, setIsLoading ] = useState(true);
  const [ pageIndex, setpageIndex ] = useState(1);
  const [ hasMore, setHasMore ] = useState(true);

  useEffect(() => {
    const fetchDate = async () => {
      try {
        setIsLoading(true);
        const response = await axios.get(`/api/chat/list?all=false&pageIndex=${pageIndex}&pageSize=20&name=`);
        if (response.data.rows.length > 0) {
          setUsers((prevData) => [...prevData, ...response.data.rows]);
          setpageIndex((prevPage) => prevPage + 1);
        } else {
          setHasMore(false);
        }
      } catch (error) {
        console.error('Error fetching data:', error);
      } finally {
        setIsLoading(false);
      }
    }
    
    if (hasMore) {
      fetchDate();
    }
  }, [ pageIndex, hasMore ]);

  function filterUser (element, index, array) {
    return element.nickname.includes(filterText);
  }

  let userList = users.filter(filterUser).map(
    user => {
      const _info = {
        name: user.nickname,
        img: user.localAvatar,
        talker: user.talker
      };
      
      return (<UserItem 
        Avatar={_info.img}
        name={_info.name}
        key={nanoid()}
        onClick={()=>props.onClickMe(_info)}
      />);
    }
  );
  return (
    <>
      <Input  className='search-input' type='text' placeholder="ÊêúÁ¥¢Áî®Êà∑..." onChange={(text)=>setFilterText(text)} />
      <div style={{ height: '770px', overflow: 'auto' }}>
        {userList}
        {isLoading && <Divider className='Divider-end' plainp>Loading...</Divider>}
        {!isLoading && !hasMore && filterText === '' && <Divider className='Divider-end' plain>Â∑≤ÁªèÂà∞Â∫ïÂï¶ ü§ê</Divider>}
      </div>
    </>
  );
}