import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import './CalendarChoose.css';
import { Spin, Calendar } from 'antd';
import { IconButton, Text} from '@chatui/core'

import locale from 'antd/es/date-picker/locale/zh_CN';
import 'dayjs/locale/zh-cn';

import dayjs from 'dayjs';
import utc from 'dayjs/plugin/utc';
import timezone from 'dayjs/plugin/timezone';

import WeChatSearch from './WeChatSearch';

dayjs.extend(utc);
dayjs.extend(timezone);
dayjs.tz.setDefault();

const CalendarBar = ({onClickBack}) => {
  return (<div className="CalendarBar">
      <IconButton className="CalendarBar--Btn" icon="chevron-left" onClick={onClickBack}/>
      <Text className="CalendarBar--Text" truncate={1} >ÊåâÁÖßÊó•ÊúüÊü•Êâæ</Text>
      <IconButton className="CalendarBar--Btn" icon="chevron-left" style={{visibility: 'hidden'}}/>
    </div>);
}

const CalendarLoading = () => {
  return (
  <div className='CalendarLoading'>
    <Spin tip="Âä†ËΩΩ‰∏≠..." size="large">
      <div className="content" />
    </Spin>
  </div>);
}

const CalendarChoose = ({info, onClickBack}) => {
  const [ messageDate, setMessageDate ] = useState([]);
  const [ isLoading, setIsLoading ] = useState(true);
  const [ isCalendarChoose, setIsCalendarChoose ] = useState(true)
  const createTimeRef = useRef(1);
  const selectTimeRef = useRef(null);
  // console.log(dayjs('2023-7-29').tz('Asia/Shanghai'));
  // console.log(dayjs('2023-7-29'));
  const disabledDate = (current) => {
    // console.log(current);
    for (let i=0; i<messageDate.length; i++) {
      if (current.isSame(dayjs(messageDate[i]))) {
        return false;
      }
    }

    return true;
  }

  useEffect(() => {
    axios.get(`/api/chat/msgdate?talker=${info.talker}`)
    .then(body=>{
      setMessageDate(body.data.date);
      setIsLoading(false);
    }).catch();
  }, [ info.talker ]);

  const CalendarElement = (<> 
    <CalendarBar onClickBack={onClickBack}/>
    {isLoading? <CalendarLoading />:
    <Calendar 
      fullscreen={false}
      disabledDate={disabledDate}
      locale={locale}
      validRange={messageDate.length > 0? [dayjs(messageDate[messageDate.length-1]), dayjs(messageDate[0])]: undefined}
      defaultValue={selectTimeRef.current? selectTimeRef.current: messageDate.length > 0? dayjs(messageDate[0]): undefined}
      onSelect={(date, info) => {
        // console.log(date);
        // console.log(info);
        if (info.source === 'date') {
          createTimeRef.current = date.valueOf(); 
          setIsCalendarChoose(false);
          selectTimeRef.current = date;
        }
      }}
    />}
  </>
  );
  return ( isCalendarChoose? CalendarElement: <WeChatSearch info={info} createTime={createTimeRef.current} onClickBack={()=>setIsCalendarChoose(true)}/>);
};
export default CalendarChoose;