import React from "react";
import { useState, useEffect } from "react";
import axios from 'axios';


import ChatUi from "./ChatUi";
import useChatMessages from "./hooks/useChatMessages";
import SearchUi from './SearchUi';
import renderMessageContent from "./RenderMessage";

export default function WeChatDialog(props) {
	
  const { messages, prependMsgs, appendMsgs } = useChatMessages([]);
  const [ pageIndex, setPageIndex ] = useState(1);
  const [ isLoading, setIsLoading ] = useState(false);
  const [ isHasMore, setIsHasMore ] = useState(true);
  const [ isSearch, setIsSearch ] = useState(false);
  
  const messageUrl = `api/chat/detail?talker=${props.info.talker}&pageIndex=${pageIndex}&pageSize=10`;

  useEffect(() => {
    setIsLoading(true);
    axios.get(messageUrl)
    .then(body=>{
      let msgs = [];
      body.data.rows.forEach(
        row => {
          msgs.unshift({
            _id: row.msgSvrId + Math.floor(Math.random() * 9999),
            type: row.type + "",
            content: row,
            position: row.isSend === 1? "right": "left",
            user: {
              avatar: row.userInfo.reserved2 !== "" ? row.userInfo.reserved2 : row.userInfo.localAvatar,
              name: row.userInfo.conRemark? row.userInfo.conRemark: row.userInfo.nickName,
            },
            hasTime: true,
            createdAt: row.createTime
          });
        }
      );

      prependMsgs(msgs);
      setIsLoading(false);
    }).catch(() => {
      setIsLoading(false);
    });
  }, [messageUrl])

	const fetchMoreData = () => {
		if (!isLoading) {
			setPageIndex(pageIndex + 1);
		}
	}

	
	return (<>
    {isSearch?
    <SearchUi info={props.info} onClickBack={()=>setIsSearch(false)}/>:
    <ChatUi
      name={props.info.name} 
      messages={messages}
      fetchMoreData={fetchMoreData}
      hasMore={isHasMore && !isLoading}
      // hasMore={false}
      renderMessageContent={renderMessageContent}
      onClickBack={props.onClickBack}
      onClickMore={()=>setIsSearch(true)}
      />}
    </>);
}