import React, { useEffect } from "react";
import { useState, useMemo, useRef } from "react";
import { IconButton, Text, Avatar, Time, Bubble } from '@chatui/core';
import InfiniteScroll from "react-infinite-scroll-component";

import dayjs from 'dayjs';

import "./chatui-theme.css"

function ChatUiBar(props) {
    return (<div className="ChatUiBar">
        <IconButton className="ChatUiBar--Btn" icon="chevron-left" onClick={props.onClickBack}/>
        <Text className="ChatUiBar--Text" truncate={1} >{props.title}</Text>
        <IconButton className="ChatUiBar--Btn" icon="ellipsis-h" onClick={props.onClickMore} style={{visibility: props.visibility? 'hidden': 'visible'}}/>
      </div>);
}

function MessageBubble(props) {
  const isLeft = props.message.position === 'right'? false: true;
  const userMessageContent = useMemo(() => props.renderMessageContent(props.message), [props.message]);

  return (<div className="MessageBubble">
    {/* <Time date={props.message.createdAt}/> */}
    <time className="Time" dateTime={dayjs(props.message.createdAt).format()}>{dayjs(props.message.createdAt).format('YYYYÂπ¥MÊúàDÊó• HH:mm')}</time>
    { userMessageContent.type === Bubble? 
      <div className={"MessageBubble-content" + (isLeft? "-left": "-right")}>
        <div className="MessageBubble-content-Avatar">
          { isLeft && <Avatar className='MessageBubble-Avatar-left' src={props.message.user.avatar} size="md" alt='Êó†Ê≥ïÂä†ËΩΩÂ§¥ÂÉè'/>}
        </div>
        <div className={"Bubble"+ (isLeft? "-left": "-right")} >
          <Text className={"MessageBubble-Name" + (isLeft? "-left": "-right")} truncate={1}>{props.message.user.name}</Text>
          {userMessageContent}
        </div>
        <div className="MessageBubble-content-Avatar">
        { !isLeft && <Avatar className='MessageBubble-Avatar-right' src={props.message.user.avatar} size="md" alt='Êó†Ê≥ïÂä†ËΩΩÂ§¥ÂÉè'/>}
        </div>
      </div>: 
      userMessageContent
    }
  </div>)
}

function ChatMessageList(props) {

  const scrollHeightRef = useRef(0);
  const scrollElementRef = useRef(null);
  const handleScroll = (event) => {
    // console.log(event.srcElement.scrollTop);
    if (event.srcElement.scrollTop === 0) {
      // console.log("at bottom");
      scrollHeightRef.current = event.srcElement.scrollHeight;
      scrollElementRef.current = event.srcElement;
      // console.log(scrollHeightRef.current);
      // console.log(scrollElementRef.current.scrollHeight);
      props.atBottom && props.atBottom();
    } else {
      scrollHeightRef.current = 0;
      scrollElementRef.current = null;
    }
  };

  function fetchMoreData(){
    // console.log("next");
    props.next();
  }

  useEffect(() => {
    if (scrollHeightRef.current !== 0 && scrollElementRef.current) {
      /* ÂõûÊªöÂà∞ÂéüÊù•ÁöÑ‰ΩçÁΩÆ */
      scrollElementRef.current.scrollTop = -(scrollElementRef.current.scrollHeight - scrollHeightRef.current);
      scrollHeightRef.current = 0;
      scrollElementRef.current = null;
    }
  }, [props.messages]);

  return (<div id="scrollableDiv">
    <InfiniteScroll 
      scrollableTarget="scrollableDiv"
      dataLength={props.messages.length}
      // next={props.next}
      next={fetchMoreData}
      hasMore={props.hasMore}
      inverse={true}
      scrollThreshold={1}
      onScroll={handleScroll}
      // style={{ display: 'flex', flexDirection: 'column-reverse' }} //To put endMessage and loader to the top.
      // style={{overflow: 'auto', overflowX: 'hidden'}}
    >
      {
        props.messages.map((msg) => {
          // console.log("props.messages.map");
          return (<MessageBubble
                    message={msg}
                    renderMessageContent={props.renderMessageContent}
                    key={msg.createdAt}
                  />);
        })
      }
    </InfiniteScroll>
  </div>
  );
}

export default function ChatUi(props) {


  return (<div className="ChatUi">
    <ChatUiBar 
      title={props.name}
      onClickMore={props.onClickMore}
      onClickBack={props.onClickBack}
      visibility={props.moreVisibility}
    />
    <ChatMessageList
      messages={props.messages}
      next={props.fetchMoreData}
      hasMore={props.hasMore} 
      renderMessageContent={props.renderMessageContent}
      atBottom={props.atBottom}
      />
  </div>)
}