import React, { useRef } from "react";
import { useState, useEffect } from "react";
import axios from 'axios';


import ChatUi from "./ChatUi";
import useChatMessages from "./hooks/useChatMessages";
import renderMessageContent from "./RenderMessage";

export default function WeChatSearch({info, createTime, onClickBack}) {
	
  const { messages, prependMsgs, appendMsgs } = useChatMessages([]);
  const [ pageIndex, setPageIndex ] = useState(1);
  const [ isLoading, setIsLoading ] = useState(false);
  const [ isHasMore, setIsHasMore ] = useState(true);
  const [ nextPageIndex, setNextPageIndex ] = useState(1);
  const itemRef = useRef(null);
  const messageUrl = `api/chat/detailat?talker=${info.talker}&createTime=${createTime}&direction=prev&pageIndex=${pageIndex}&pageSize=10`;

  useEffect(() => {
    setIsLoading(true);
    axios.get(messageUrl)
    .then(body=>{
      let msgs = [];
      body.data.rows.forEach(
        row => {
          msgs.unshift({
            _id: row.msgSvrId + Math.floor(Math.random() * 9999),
            type: row.type + "",
            content: row,
            position: row.isSend === 1? "right": "left",
            user: {
              avatar: row.userInfo.reserved2 !== "" ? row.userInfo.reserved2 : row.userInfo.localAvatar,
              name: row.userInfo.conRemark? row.userInfo.conRemark: row.userInfo.nickName,
            },
            hasTime: true,
            createdAt: row.createTime
          });
        }
      );

      prependMsgs(msgs);
      setIsLoading(false);
    }).catch(() => {
      setIsLoading(false);
    });
  }, [messageUrl])

  const nextMessageUrl = `api/chat/detailat?talker=${info.talker}&createTime=${createTime}&direction=next&pageIndex=${nextPageIndex}&pageSize=10`;

  useEffect(() => {
    setIsLoading(true);
    axios.get(nextMessageUrl)
    .then(body=>{
      let msgs = [];
      body.data.rows.forEach(
        row => {
          msgs.push({
            _id: row.msgSvrId + Math.floor(Math.random() * 9999),
            type: row.type + "",
            content: row,
            position: row.isSend === 1? "right": "left",
            user: {
              avatar: row.userInfo.reserved2 !== "" ? row.userInfo.reserved2 : row.userInfo.localAvatar,
              name: row.userInfo.conRemark? row.userInfo.conRemark: row.userInfo.nickName,
            },
            hasTime: true,
            createdAt: row.createTime
          });
        }
      );
      
      appendMsgs(msgs);
      setIsLoading(false);
    }).catch(() => {
      setIsLoading(false);
    });
  }, [nextMessageUrl])

  useEffect(() => {
    setIsLoading(true);
    const prevMessageUrl = `api/chat/detailat?talker=${info.talker}&createTime=${createTime}&direction=prev&pageIndex=${pageIndex}&pageSize=10`;
    const nextMessageUrl = `api/chat/detailat?talker=${info.talker}&createTime=${createTime}&direction=next&pageIndex=${pageIndex}&pageSize=10`;

    const fetchData = async () => {
      try {
        const [ prevResponse, nextResponse ] = await Promise.all([
          axios.get(prevMessageUrl),
          axios.get(nextMessageUrl)
        ]);

        const prevData = prevResponse.data;
        const nextData = nextResponse.data;
        let msgs = [];
        if (prevData) {
          prevData.rows.forEach(
            row => {
              msgs.unshift({
                _id: row.msgSvrId + Math.floor(Math.random() * 9999),
                type: row.type + "",
                content: row,
                position: row.isSend === 1? "right": "left",
                user: {
                  avatar: row.userInfo.reserved2 !== "" ? row.userInfo.reserved2 : row.userInfo.localAvatar,
                  name: row.userInfo.conRemark? row.userInfo.conRemark: row.userInfo.nickName,
                },
                hasTime: true,
                createdAt: row.createTime
              });
            }
          );
        }

        if (nextData) {
          const index = msgs.length;
          nextData.rows.forEach(
            row => {
              msgs.push({
                _id: row.msgSvrId + Math.floor(Math.random() * 9999),
                type: row.type + "",
                content: row,
                position: row.isSend === 1? "right": "left",
                user: {
                  avatar: row.userInfo.reserved2 !== "" ? row.userInfo.reserved2 : row.userInfo.localAvatar,
                  name: row.userInfo.conRemark? row.userInfo.conRemark: row.userInfo.nickName,
                },
                hasTime: true,
                createdAt: row.createTime
              });
            }
          );
          itemRef.current = msgs[index]._id;
          // console.log(msgs);
          appendMsgs(msgs);
        }
        
        setIsLoading(false);
      } catch (error) {
        console.error('Error fetching data:', error);
        setIsLoading(false);
      }
    };

    fetchData();
  }, [])

  useEffect(() => {
    const elem = document.getElementById(itemRef.current);
    if (elem) {
      let currentNode = elem;
      while (currentNode.parentNode) {
        currentNode = currentNode.parentNode;
        if (currentNode.className === 'MessageBubble') {
          currentNode.scrollIntoView();
          itemRef.current = null;
          break;
        }
      }
    }
  }, [messages]);

	const fetchPrevData = () => {
		if (!isLoading) {
			setPageIndex(pageIndex + 1);
		}
	}

  const fetchNextData = () => {
    if (!isLoading) {
			setNextPageIndex(nextPageIndex + 1);
		}
  }
	
	return (<>
    <ChatUi
      name={info.name} 
      messages={messages}
      fetchMoreData={fetchPrevData}
      hasMore={isHasMore && !isLoading}
      // hasMore={false}
      renderMessageContent={renderMessageContent}
      onClickBack={onClickBack}
      moreVisibility='hidden'
      atBottom={fetchNextData}
      />
    </>);
}