function timeStampToDate(stamp) {
	const date = new Date(stamp); // ‰º†ÂÖ•ÊØ´ÁßíÊó∂Èó¥Êà≥ÂàõÂª∫ Date ÂØπË±°

	// ‰ΩøÁî® Date ÂØπË±°ÁöÑÊñπÊ≥ïËé∑ÂèñÂπ¥„ÄÅÊúà„ÄÅÊó•Á≠â‰ø°ÊÅØ
	const year = date.getFullYear();
	const month = date.getMonth() + 1; // Êúà‰ªΩ‰ªé 0 ÂºÄÂßãÔºåÊâÄ‰ª•Ë¶ÅÂä† 1
	const day = date.getDate();
	const hours = date.getHours();
	const minutes = date.getMinutes();
	const seconds = date.getSeconds();

	// ÂèØ‰ª•Ê†πÊçÆÈúÄË¶ÅÂ∞ÜËøô‰∫õÊó∂Èó¥ÈÉ®ÂàÜÊ†ºÂºèÂåñÊàêÂ≠óÁ¨¶‰∏≤
	// const formattedDate = `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
	// console.log(formattedDate);
	return `${year}-${month}-${day}`;
}

