function UserItemList(props) {

	const [ items, setItems ] = useState([]);
	const [ isHasMore, setIsHasMore ] = useState(true);
	// const [ createTime, setCreateTime ] = useState(Date.now());
	

	useEffect(() => {
		setItems([]);
		console.log('useEffect');
		setIsHasMore(true)
		fetchMoreData(false)
	}, [ props.keyWord ]);

	function onClikItem(item) {
		console.log(item)
		props.onClikItem(item)
	}

	function fetchMoreData(isconcat) {
		let createTime = (isconcat == false)? Date.now(): items.length > 0? items[items.length-1].key: Date.now();
		console.log("createTime: ", createTime)
		axios.get(`api/chat/detailkw?talker=${props.user}&createTime=${createTime}&pageSize=20&keyWord=${props.keyWord}`)
    .then(body=>{
			if (body.data.total === 0) {
				setIsHasMore(false);
				console.log("setIsHasMore(false);");
				return;
			}
			let itemlist = [];
			body.data.rows.forEach(
				row => {
					itemlist.push({
						text: row.content,
						date: timeStampToDate(row.createTime),
						Avatar: row.userInfo.reserved2 !== "" ? row.userInfo.reserved2 : row.userInfo.localAvatar,
						name: row.userInfo.conRemark? row.userInfo.conRemark: row.userInfo.nickName,
						key: row.createTime,
					});
			})
			// console.log(items);
			setItems(isconcat? items.concat(itemlist): itemlist);
			// setItems(itemlist);
    }).catch();
	}

	return (
	<div className='keyWord--InfiniteScroll--outside'>
	<InfiniteScroll
	className='keyWord--InfiniteScroll'
		dataLength={items.length}
		hasMore={isHasMore}
		next={()=>{console.log("fetchMoreData(true)");fetchMoreData(true)}}
		height={770}
		// loader={<h4>Loading...</h4>}
	>
		<div className='keyWord--InfiniteScroll--div'>
		{items.map((item) => {
			return (<UserItem
				onClick={()=>onClikItem(item)}
				Avatar={item.Avatar}
				name={item.name}
				text={item.text}
				date={item.date}
				key={item.key}
				/>)
		})}
		</div>
	</InfiniteScroll>
	</div>)
}

export default function SearchUi(props) {

	const [currentComponent, setCurrentComponent] = useState('Main');
	const [ keyWord, setKeyWord ] = useState('');
	const [ isSearchBar, setIsSearchBar ] = useState(true);
	const createTimeRef = useRef(0);

	function onClickButton(btn) {
		switch (btn) {
			case 'Back': props.onClickBack();
				break;
			case 'Date': setCurrentComponent('Date');
				break;
			case 'Img': setCurrentComponent('Img');
				break;
			default: console.log('unknown click ', btn);
				break;
		}
	}
	const onClikItem = (item) => {
		createTimeRef.current = item.key-1;
		setIsSearchBar(false);
	};
	const SearchBarComponent = (<>
		<SearchBar onChangeKeyWord={(kw)=>{setKeyWord(kw);}} onClickButton={onClickButton}/>
		{ keyWord === "" ? <SearchOption onClickButton={onClickButton} />: <UserItemList keyWord={keyWord} user={props.info.talker} onClikItem={onClikItem}/>}
	</>);

	function renderComponent() {
		switch(currentComponent) {
			case 'Main':
				return (<>
					{ isSearchBar ? SearchBarComponent: <WeChatSearch info={props.info} createTime={createTimeRef.current} onClickBack={()=>setIsSearchBar(true)}/>
				}
				</>);
			case 'Date':
				return (<CalendarChoose info={props.info} onClickBack={()=>setCurrentComponent('Main')}/>);
			case 'Img':
				return (<ImagesUI talker={props.info.talker} onClickBack={()=>setCurrentComponent('Main')}/>);
			default:
				return (<div>unknown</div>);
		}
	}
  return (<>
		{renderComponent()}
	</>)
}