import React, { useEffect, useState, useMemo } from 'react';
import { IconButton, Text, Icon } from '@chatui/core';
import { Image, FloatButton, Tag } from 'antd';
import { nanoid } from 'nanoid';
import axios from 'axios';
import InfiniteScroll from "react-infinite-scroll-component";
import useChatMessages from "./hooks/useChatMessages";

import './imageui-theme.css'



const ImagesUIBar = ({onClickBack}) => {
  return (<div className="ImagesUIBar">
      <IconButton className="ImagesUIBar--Btn" icon="chevron-left" onClick={onClickBack}/>
      <Text className="ImagesUIBar--Text" truncate={1} >ÂõæÁâáÂèäËßÜÈ¢ë</Text>
      <IconButton className="ImagesUIBar--Btn" icon="chevron-left" style={{visibility: 'hidden'}}/>
    </div>);
}

function ImageItem({info}) {
  const fn = (info) => {
    return(
      <div className="preview-ImageItem">
        <Image className='preview-ImageItem-img' 
          width={96} height={128} src={info.type === 'image'? info.src: info.preview} loading='lazy'
        />
        <Icon className='preview-ImageItem-icon' type={info.type === 'image'? 'image': 'play-circle'} />
      </div>
    );
  }
  const comp = useMemo(() => fn(info), [info]);
  
  return comp;
}

export default function ImagesUI({talker, onClickBack}) {
  
  const { messages, prependMsgs, appendMsgs } = useChatMessages([]);
  const [ pageIndex, setPageIndex ] = useState(1);
  const [ isLoading, setIsLoading ] = useState(false);
  const [ isHasMore, setIsHasMore ] = useState(true);
  
  const mediaUrl = `apt/media/list?talker=${talker}&pageIndex=${pageIndex}&pageSize=28`;

  useEffect(() => {
    setIsLoading(true);
    axios.get(mediaUrl)
    .then(body=>{
      let msgs = [];
      body.data.rows.forEach(
        row => {
          msgs.push({
            type: row.type === 3? 'image': 'video',
            preview: row.type === 3? row.mediaPath: row.imgPath,
            src: row.type === 3? (row.mediaSourcePath || row.mediaBCKPath || row.mediaPath): row.mediaPath,
            createdAt: row.createTime,
          });
        }
      );
      
      if (msgs.length === 0) {
        setIsHasMore(false);
      } else {
        appendMsgs(msgs);
      }
      setIsLoading(false);
    }).catch(() => {
      setIsLoading(false);
    });
  }, [mediaUrl])

  const fetchMore = () => {
    if (!isLoading) {
      setPageIndex(pageIndex+1);
    }
  };
  
  return (
  <>
    <ImagesUIBar onClickBack={onClickBack}/>
    <InfiniteScroll
      className='ImageUI--InfiniteScroll'
      dataLength={messages.length}
      hasMore={isHasMore}
      next={fetchMore}
      height={770}
      // loader={<h4>Loading...</h4>}
      endMessage={<div className='endMessage-div'><p>- Â∑≤ÁªèÂà∞Â∫ïÂï¶ -</p></div>}
    >
      <div className='ImageUI-list'>
        <Image.PreviewGroup
          preview={{
            onChange: (current, prev) => console.log(`current index: ${current}, prev index: ${prev}`),
            imageRender: (originalNode, info) => {
              if (messages.length === 0) {
                return originalNode;
              }

              if (messages[info.current].type === 'image') {
                // return (<img src={messages[info.current].src}/>);
                return originalNode;
              }
              
              return (<video className='video_view' height='100%' width='100%' controls
                src={messages[info.current].src}
              />);
              },
            toolbarRender: (originalNode, info) =>{
              return (messages[info.current].type == 'image')? originalNode: null;
            },
            onVisibleChange: (visible, prevVisible, current) => {
              if (visible === false && prevVisible === true && messages[current].type === 'video') {
                const vid = document.getElementsByClassName("video_view");
                console.log(vid);
                vid[0].pause();
              }
              console.log('visible', visible, 'prevVisible', prevVisible, 'current', current);
            },
            
          }}
        >
          {messages.map((img) => { return <ImageItem info={img} key={nanoid()}/>})}
        </ Image.PreviewGroup>
        
      </div>
      <FloatButton.BackTop />
    </ InfiniteScroll>
  </>
  );
}
